import Request from '@/axios/request'

export function getRecordPlans (params = {}) {
  return Request.get('record/plans', { params })
}

export function addRecordPlan (formData) {
  return Request.promisePost('record/addPlan', formData)
}

export function editRecordPlan (id, formData) {
  return Request.promisePost('record/editPlan', formData, { params: { id: id } })
}
export function getRecordPlan (id) {
  return Request.get('record/plan', { params: { id: id } })
}

export function delRecordPlan (id) {
  return Request.promisePost('record/delPlan', { id: id })
}

export function getRecordPlanItems (params = {}) {
  return Request.get('record/planItems', { params })
}

export function getRecordChannels (params = {}) {
  return Request.get('record/recordChannels', { params })
}

export function getRecordFiles (params = {}) {
  return Request.get('record/files', { params })
}
export function getSummaryList (params = {}) {
  return Request.get('record/summary', { params })
}

export function startPlayBack(id) {
  return Request.promisePost('record/startPlayBack', {video_id: id})
}

export function closePlayBack(id) {
  return Request.promisePost('record/closePlayBack', {video_id: id})
}

export function delPlayBack(id) {
  return Request.promisePost('record/delPlayBack', {video_id: id})
}