<template>
  <a-modal
    :title="title"
    :width="640"
    :visible="visible"
    :confirmLoading="loading"
    @ok="
      () => {
        $emit('ok')
      }
    "
    @cancel="
      () => {
        $emit('cancel')
      }
    "
  >
    <a-spin :spinning="loading">
      <a-form-model ref="bindRpForm" :model="model" v-bind="formItemLayout">
        <a-form-model-item label="录像计划模板" prop="record_plan_id" help="如果未选择录像计划模板提交保存，则表示解绑">
          <a-select v-model="model.record_plan_id">
            <a-select-option value="0">请选择</a-select-option>
            <a-select-option v-for="(plan, key) in recordPlanItems" :value="plan.id" :key="key">{{
              plan.name
            }}</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-spin>
  </a-modal>
</template>

<script>
import { getRecordPlanItems } from '@/api/record'

const defaultModel = {
    record_plan_id: '0'
}
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    data () {
        return {
            title: '绑定录像计划',
            model: Object.assign(defaultModel, {}),
            recordPlanItems: {},
            formItemLayout: {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 }
            }
        }
    },
    methods: {
        async getRecordPlanItems () {
          this.recordPlanItems = await getRecordPlanItems()
          console.log('====================================')
          console.log('recordPlanItems:', this.recordPlanItems)
          console.log('====================================')
        },
        resetForm () {
            this.model = Object.assign(defaultModel, {})
            this.$nextTick(() => {
                if (this.$refs.bindRpForm) {
                    this.$refs.bindRpForm.resetFields()
                }
            })
        }
    }
}
</script>
<style lang="less" scoped>
.ant-select {
    min-width: 220px;
}
</style>
